
<template>

    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-0">
                            <div class="theme-primary partition-full">
                                <span class="p-name text-white">{{ $t(submodulecode) }}</span>
                            </div>
                            <button type="button" class="close" @click=closepopup()>
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="border p-3 scroll">
                            <Form ref="myForm">
                            <dynamic-form lang="en"  ref="ManageForm"  :buttons="buttons" :schema="FormSchema" v-if="formdoesnotexsist==false">
                                <template v-slot:tgslot-comment="{ data }">
                                    <ckeditor :editor="editor"   @ready="onReady" v-model="data.fieldInfo.value"
                                        @input="updateCkeditorModel($event, data)" :class="{ 'is-invalid': data.error != '' }">
                                    </ckeditor>
                                    <span class="invalid-feedback">{{ data.error }}</span>
                                    <input type="hidden" v-model="data.fieldInfo.value" />
                                </template>
                            </dynamic-form>
                            <!-- <div v-if="formdoesnotexsist" class="text center danger ">Form doesnt exsist </div> -->

                            <div  v-if="formdoesnotexsist" class="alert alert-danger py-0 text-center" role="alert">
                                Form does not exsist.
                             </div>
                       </Form>
                         
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        getvalues: {
            type: Object,
            required: true
        },
        submodulename:{
            type: String,
            required: true
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            formdoesnotexsist:false,
            isLoading: false,
            // Id:this.$route.params.id,
            UserName: null,
            UserId: null,
            FormSchema: [],
            modulename: '',
            submodulecode: '',
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
                onClick: this.Submit
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.closepopup,
                isVisible: true,
            },
            ],
            // isSuccessMessage: false,
        }
    },
    async created() {
        debugger;
        var vm = this;
        vm.UserId = vm.GetUserInfo.ID;
        vm.UserName = vm.GetUserInfo.Name;
        vm.getForm(vm.getvalues);
    },
    methods: {
        closepopup() {
            var vm = this;
            vm.$parent.IsManageForm = false;
        },
        getForm: function (values) {

            var ths = this;
            
            ths.isLoading = true;
            ths.modulename = values.module_name;
            ths.submodulecode = values.sub_module_code
            var param = `id=&moduleName=${values.module_name}&subModuleCode=${values.sub_module_code}&otherData=&langCode=en&CustomLayoutId=&usefor=QuickAdd`;
            DataService.GetFormCustomLayout(param).then(function (response) {

                if (response != null && response.data != null && response.data != "") {
                    ths.formdoesnotexsist=false;
                    var frmSchma = ths.$refs.ManageForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    if(values.sub_module_code == 'Ticketing'){
                        frmSchma.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (field.name == "is_read") {
                                    field.value = 0;
                                }
                                if (field.name == "status_id") {
                                    if(field.config.options.length > 0)
                                    {
                                        field.value = 1111;
                                    }                               
                                }
                                if (field.name == "ticket_for" && ths.$route.params.id == "0") {
                                    field.value = "1";
                                }
                            })
                        });
                    }
                    ths.FormSchema = frmSchma
                    ths.isLoading = false;
                }
                else{
                    ths.isLoading = false; 
                    ths.formdoesnotexsist=true;           
                }
            });
        },

        
        Submit () {
            
            var ths = this;
            ths.isLoading = true;
            var CustomFieldArray = ths.$refs.ManageForm.ConvertDynamicFormObjToCustomFieldObj(ths.FormSchema);
            var obj = {
                data: CustomFieldArray,
                moduleCode: ths.modulename,
                subModuleCode: ths.submodulecode
            }
            DataService.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
            if (response != null && response.data != null && response.data[0].Status == "Success" && response.data[0].Value.MSG != 'FAILURE') {
                ths.ShowAlert('Form save Successfully', "success", true, ths.$t("Alert"));
            }
            })
            ths.isLoading = false;
        }
    }
}
</script>
